.rdl-mediaplayer {
	&.theme-bg {
		padding: 16px 12px;
		margin-bottom: 24px;
	}
	&.page-video {
		display: contents;
		h3, p {
			display: none;
		}
	}
	h3 {
		margin-top: 16px;
	}
}
.page-container {
	.rdl-mediaplayer {
		&.page-video {
			.embed-responsive {
				margin: 0 12px 24px;
			}
		}
	}
}