/*------------------------------------*\
   	buttons
\*------------------------------------*/
.btn {
	text-transform: none;
	margin-bottom: 12px;
	@extend %regular-sans-serif;

	&-outline-primary {
		&:not(:disabled):not(.disabled):active,
		&:not(:disabled):not(.disabled).active,
		.show > &.dropdown-toggle {
			background-color: $dark-blue-05;
			color: $dark-blue-100;
		}
	}
	&-outline-primary:hover,
	&-outline-primary:focus {
		background-color: $dark-blue-05;
		color: $dark-blue-100;
	}
	&.disabled, &:disabled {
		background-color: $almost-black-50;
		border-color: $almost-black-50;
	}
	&-danger, &-danger:hover, &-danger:focus {
		color: $almost-black-100;
	}
	//used for ie. archive filters
	&-link {
		text-transform: uppercase;
		font-size: 1.25rem;
		line-height: 1.75rem;
		padding: 0 8px;
		text-decoration: none;
		border: none;
		color: $primary;
		&:hover, &:focus {
			text-decoration: none;
		}
		&.disabled, &:disabled {
			background-color: transparent;
		}
	}
	&:focus,
	&.focus {
		box-shadow: none;
		outline: auto 5px -webkit-focus-ring-color;
	}
	&:-moz-focusring {
		outline: 1px dotted;
	}
	.theme-bg & {
		&-outline-primary:hover,
		&-outline-primary:focus {
			background-color: #fff;
		}
	}
	&-icon {
		display: inline-flex;
		align-items: center;
		padding-top: 12px;
		padding-right: 8px;
		padding-bottom: 12px;

		span, i {
			margin-left: auto;
		}
		i {
			font-size: 1.375rem;
			padding-left: 46px;
		}
	}
	&-sm-block {
		width: 100%;
		@include media-breakpoint-up(md) {
			width: auto;
		}
	}

	// form selects
	&-outline-primary.dropdown-toggle {
		border-color: $almost-black-50;
	}
	&-dense {
		padding-right: 12px;
		padding-left: 12px;
		i {
			padding: 0;
		}
	}
	&-prev {
		padding-left: 6px;
	}
	&-next {
		padding-right: 6px;
	}
}


/*------------------------------------*\
    icons as buttons
\*------------------------------------*/
button.icon-button {
	border: none;
	padding: 8px;
	background-color: transparent;
	&:hover {
		background-color: transparent;
	}
	i {
		vertical-align: bottom;
	}
}


/*------------------------------------*\
    pill/filter buttons
\*------------------------------------*/
.btn.badge-pill {
	padding-top: 9px;
	padding-bottom: 8px;
	margin-right: 12px;
	&:hover, &:focus {
		color: $primary;
		background-color: darken($dark-blue-05, 10%);
	}
	&.active {
		background-color: $primary;
		color: #fff;
	}
	.theme-bg & {
		background-color: #fff;
		color: $dark-gray;
		&:hover, &:focus {
			color: $primary;
			background-color: $dark-blue-05;
		}
		&.active {
			background-color: $primary;
			color: #fff;
		}
	}
}


/*------------------------------------*\
    slim buttons - switch, download
\*------------------------------------*/
.btn-slim {
	padding: 7px 12px;
	display: inline-flex;
	align-items: center;
}


/*------------------------------------*\
    disabled hover on buttons w active class - ie switch
\*------------------------------------*/
.btn-primary.active:hover,
.btn-primary.active:focus,
.btn-primary:not(:disabled):not(.disabled).active {
	background-color: $primary;
}


/*------------------------------------*\
    download button
\*------------------------------------*/
.download-button {
	display: inline-flex;
	align-items: center;
	margin-bottom: 12px;
	> i {
		margin-right: 16px;
		color: $primary;
		&.rdl-icons {
			font-size: 2.25rem;
		}
		&.material-icons {
			font-size: 2.5rem;
		}
	}
	.btn {
		margin-bottom: 0;
	}
}