// spots w backgorund-color
.rdl-spot.theme-bg {
	padding:  16px 12px;
}

// simulating block links
.rdl-overlay-link {
	color: transparent;
	display: inline-block;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 50;
	&:hover, &:focus {
		color: transparent;
	}
}



// spots general
.rdl-spot {
	margin-bottom: 24px;
	position: relative;

	.rdl-overlay-link {
		&.with-link {
			bottom: 32px;
		}
		&:hover {
			~ p, ~ div span, ~ h3, ~ ul {
				color: $primary;
			}
			~ figure .btn-danger {
				background-color: darken($red-100, 10%);
				border-color: darken($red-100, 10%);
			}
			~ figure .btn-warning {
				background-color: darken($orange, 10%);
				border-color: darken($orange, 10%);
			}
		}
	}
	em {
		font-family: noway-italic, sans-serif;
	}
	strong, b {
		font-family: nowaymedium, sans-serif;
	}
}


// content spots
.rdl-spot {
	p {
		margin-bottom: 16px;
	}
	&-content {
		max-width: 630px;
		display: flex;
		flex-direction: column;

		figure {
			order: -1;
			position: relative;
			margin-bottom: 24px;
		}
		figure .btn {
			margin-bottom: 0;
			position: absolute;
			left: 24px;
			bottom: 24px;
		}
		figure.skewed {
			&::after {
				content: '';
				display: block;
				overflow-x: hidden;
				width: 101%;
				height: 28px;
				margin: -20px 0;
				background: white;
				transform: skewY(1deg);
			}
		}

		footer {
			display: flex;
			align-items: center;
			font-family: 'noway', sans-serif;
			font-weight: normal;
			font-size: 0.875rem;
			a {
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				padding-inline-end: 4px;
			}
		}
		time {
			display: inline-block;
			text-transform: uppercase;
			color: $almost-black-50;
			margin-bottom: 24px;
		}
	}
}
.skewed-spots {
	figure::after {
		content: '';
		display: block;
		overflow-x: hidden;
		width: 101%;
		height: 28px;
		margin: -20px 0;
		background: white;
	}

	&:nth-child(even) figure::after {
		transform: skewY(-1deg);
	}
	&:nth-child(odd) figure::after {
		transform: skewY(1deg);
	}
}


// download spot
.rdl-spot {
	&-download {
		@extend %regular-sans-serif;
		h3 {
			margin-bottom: 1rem;
		}
		span {
			display: inline-flex;
		}
		p {
			margin-bottom: 24px;
		}
		.download-button {
			z-index: 100;
			margin-bottom: 28px;
		}
		.file-information {
			margin-bottom: 16px;
			span {
				margin-right: 16px;
				margin-bottom: 16px;
				align-items: center;
			}
			span p {
				display: inline-flex;
				font-size: 0.875rem;
				margin-bottom: 0;
			}
			i {
				margin-right: 8px;
			}
		}
		&.with-image {
			img {
				margin-bottom: 22px;
			}
			.file-information {
				clear: both;
			}
			@include media-breakpoint-up(md) {
				h3 {
					max-width: 54%;
					display: inline-block;
				}
				img {
					max-width: 41%;
					float: right;
					margin-left: 20px;
				}
			}
		}
	}
}
aside {
	.rdl-spot {
		&-download.with-image {
			h3 {
				max-width: none;
			}
			img {
				max-width: 100%;
				float: none;
				margin-left: 0;
			}
		}
	}
}


// login/link spot
.rdl-spot {
	&-link {
		@extend %regular-sans-serif;
		i {
			color: $primary;
			font-size: 1.25rem;
			float: right;
			line-height: 1.5rem;
		}
		h3 {
			padding-right: 32px;
		}
	}
}

div[class^="col"] .rdl-spot-link:first-child {
	height: calc(100% - 24px);
}


// search spot
.rdl-spot {
	&-search {
		@extend %regular-sans-serif;
		@include media-breakpoint-up(lg) {
			margin-bottom: 48px;
		}
	}
}

aside .rdl-spot,
aside .rdl-spot p {
	margin-bottom: 0;
}


// focus spot
.rdl-spot {
	&-focus,
	&-focus.theme-bg {
		h3 {
			color: $almost-black-100;
		}
		h3.primary-color {
			color: $primary;
		}
	}
}


// info spots - data and message
.rdl-spot {
	&-message,
	&-data {
		i {
			color: $primary;
		}
		dt {
			@extend %regular-sans-serif;
		}
		dd {
			margin-bottom: 0;
		}
	}
}