$skewed-xs: 18px;
$skewed-sm: 24px;
$skewed-md: 28px;
$skewed-lg: 36px;
$skewed-xl: 42px;
$skewed-xxl: 58px;


// full width containers with background



// parallel - to left or right
.parallel-down,
.parallel-up {
	margin-bottom: $skewed-sm;
	&::before,
	&::after {
		content: '';
		display: flex;
		position: absolute;
		z-index: 75;
	}
	@include media-breakpoint-up(md) {
		margin-bottom: $skewed-md;
	}
}

.parallel-down {
	&::before {
		border-top: $skewed-sm solid white;
		border-left: 100vw solid transparent;
	}
	&::after {
		margin-top: -$skewed-sm;
		border-top: $skewed-sm solid transparent;
		border-left: 100vw solid #fff;
	}
	> *:first-of-type {
		padding-top: $skewed-lg;
	}
	> *:last-of-type {
		padding-bottom: $skewed-xl;
	}
	.rdl-hero-content {
		padding-top: 24px;
		padding-bottom: 0;
	}
}

.parallel-up {
	&::before {
		border-top: $skewed-sm solid white;
		border-right: 100vw solid transparent;
	}
	&::after {
		border-top: $skewed-sm solid transparent;
		border-right: 100vw solid white;
		margin-top: -$skewed-sm;
	}
	> *:first-of-type {
		padding-top: $skewed-xxl;
	}
	> *:last-of-type {
		padding-bottom: $skewed-sm;
	}
	.rdl-hero-content {
		padding-top: 24px;
		padding-bottom: 0;
	}
}


// trapeze - bigger in one side, smaller in the other - to left or right
.trapeze-big-to-small,
.trapeze-small-to-big {
	&::before,
	&::after {
		content: '';
		display: flex;
		position: absolute;
		z-index: 75;
	}
	@include media-breakpoint-up(md) {
		margin-bottom: $skewed-md;
	}
}

.trapeze-small-to-big {
	&::before {
		border-bottom: $skewed-sm solid transparent;
		border-left: 100vw solid #fff;
	}
	&::after {
		border-top: $skewed-sm solid transparent;
		border-left: 100vw solid #fff;
		margin-top: -$skewed-sm;
	}
	> *:first-of-type {
		padding-top: $skewed-xxl;
	}
	> *:last-of-type {
		padding-bottom: $skewed-xl;
	}
}

.trapeze-big-to-small {
	&::before {
		border-bottom: $skewed-sm solid transparent;
		border-right: 100vw solid #fff;
	}
	&::after {
		margin-top: -$skewed-sm;
		border-top: $skewed-sm solid transparent;
		border-right: 100vw solid #fff;
	}
	> *:first-of-type {
		padding-top: $skewed-lg;
	}
	> *:last-of-type {
		padding-bottom: $skewed-sm;
	}
}



// bottom-up and bottom-down - for bottom content just before footer (only top edge is skewed)
.bottom-up,
.bottom-down {
	&::before {
		content: '';
		display: flex;
		position: absolute;
		z-index: 75;
	}
	> *:last-of-type {
		padding-bottom: $skewed-xs;
	}
}
.bottom-down {
	&::before {
		border-top: $skewed-sm solid white;
		border-left: 100vw solid transparent;
	}
	> *:first-of-type {
		padding-top: $skewed-lg;
	}
}
.bottom-up {
	&::before {
		border-top: $skewed-sm solid white;
		border-right: 100vw solid transparent;
	}
	> *:first-of-type {
		padding-top: $skewed-xxl;
	}
}