.rdl-hero, .rdl-slice {
	position: relative;
	img {
		width: 100%;
	}
	.rdl-hero-content {
		color: #fff;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;

		.display-1 {
			font-size: calc(1rem + 4vw);
			line-height: calc(1.5rem + 4vw);
			margin-bottom: 0;
		}
		&.with-tagline .display-1 {
			font-size: calc(1rem + 2.5vw);
			line-height: calc(1.5rem + 2.5vw);
		}
		.lead, h2 {
			font-family: 'noway', sans-serif;
			font-weight: normal;
			font-size: calc(1rem + 1.25vw);
			line-height: calc(1.25rem + 1.25vw);
			margin-bottom: 0;
		}
		.lead {
			margin-bottom: 1%;
		}
		h2 {
			margin-top: 1%;
		}
	}
	&::before,
	&::after {
		content: '';
		display: flex;
		position: absolute;
		z-index: 75;
	}
	&.text-right .rdl-hero-content {
		margin-left: auto;
		direction: rtl;
	}
}

.rdl-hero {
	&::after {
		margin-top: -23px;
		border-right: 100vw solid transparent;
		border-bottom: 24px solid white;
	}
}

.rdl-slice {
	margin-bottom: 24px;
}


// Move content up over hero (frontpage) and over header (subpages)
.hero-overlay {
	margin-top: 24px;
}

@include media-breakpoint-up(lg) {
	header.sub-page + main {
		margin-top: -36px;
		position: relative;
		z-index: 125;
	}
}

@include media-breakpoint-up(xl) {
	.hero-overlay .container {
		position: relative;
		background: #fff;
		margin-top: -108px;
		min-height: 108px;
		padding-top: 24px;
		z-index: 105;
	}
	.rdl-hero .rdl-hero-content {
		padding-bottom: 48px;
	}
}

@include media-breakpoint-up(xxl) {
	.rdl-hero .rdl-hero-content {
		padding-bottom: 0;
	}
}