// inherits from spots.scss

.rdl-spot-fact {
	margin-bottom: 36px;

	button {
		position: absolute;
		bottom: -30px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;
		background-color: $primary;
		color: white;
		width: 36px;
		height: 36px;
		border-radius: 0;
		&::before {
			content: '+';
			font-size: 24px;
		}
		&.active::before {
			content: '-';
			font-size: 36px;
		}
		&::after {
			content: '';
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 12px 18px 0 18px;
			border-color: $primary transparent transparent transparent;
			position: absolute;
			bottom: -12px;
			left: 0;
		}
		&:hover {
			background-color: $primary;
			color: white;
		}
	}
	button + .fact-content {
		max-height: 110px;
		transition: max-height 0.5s;
	}
	.fact-content {
		margin-bottom: 64px;
		padding: 26px 18px;
		overflow: hidden;
		&::after {
			content: '';
			display: block;
			background: transparent;
			background-image: linear-gradient(to top, white, rgba(255, 255, 255, 0));
			width: 100%;
			height: 50px;
			margin: 0;
			transform: none;
			position: absolute;
			bottom: 0;
			left: 0;
		}
	}
	button.active::after {
		top: -12px;
		transform: rotate(180deg);
	}
	button.active + .fact-content {
		height: 100%;
		max-height: 1000px;
		transition: max-height 1s;
		&::after {
			overflow-x: hidden;
			width: 101%;
			height: 28px;
			margin: -20px 0;
			background: white;
			transform: skewY(-1.5deg);
		}
	}
	h3 {
		color: $almost-black-100;
	}

	a {
		display: block;
		margin: 24px 0;
	}
	small {
		display: block;
		margin-bottom: 18px;
	}
	.img-fluid {
		max-width: none;
		width: 100%;
	}
	@include media-breakpoint-up(lg) {
		max-width: 630px;
		&.offset-content-left,
		&.offset-content-right {
			max-width: 492px;
			button {
				display: none;
			}
			button.active + .fact-content,
			.fact-content {
				height: 100%;
				max-height: 1000px;
				transition: max-height 1s;
				position: relative;
				&::after {
					overflow-x: hidden;
					width: 101%;
					height: 28px;
					margin: -20px 0;
					background: white;
					transform: skewY(-1.5deg);
				}
			}
		}
	}
}